import React from 'react';
import { useField } from 'formik';
import { TextFieldWithLabel, Box } from '@cko/primitives';
import FieldValidationError from 'components/FieldValidationError';

interface Props {
  label: string;
  mb?: number;
  placeholder?: string;
  name: string;
  type?: 'text' | 'number';
  submitCount: number;
  displayFieldError?: boolean;
  disabled?: boolean;
  onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
}

const TextField: React.FunctionComponent<Props> = ({
  label,
  mb = 2,
  name,
  type = 'text',
  submitCount,
  displayFieldError = true,
  ...props
}) => {
  const [field, meta] = useField(name);
  const { error: errorMessage } = meta;
  const showError = submitCount > 0 && errorMessage !== undefined;

  return (
    <Box mb={mb}>
      <TextFieldWithLabel
        style={{ fontSize: '13px' }}
        title={label}
        type={type}
        error={showError}
        {...field}
        {...props}
      />
      {displayFieldError && showError && (
        <FieldValidationError data-testid={`${field.name}-error-messsage`}>
          {errorMessage}
        </FieldValidationError>
      )}
    </Box>
  );
};

export default TextField;
