import React from 'react';
import { Flex, Box, Text } from '@cko/primitives';
import SearchInput from 'components/SearchInput';
import styled from 'styled-components';

const pluralise = (singular: string, plural: string, count: number) =>
  count === 1 ? singular : plural;

const VerticalLine = styled(Box)`
  margin: 0 12px;
  height: 24px;
  width: 1px;
  background-color: ${(props) => props.theme.colors.baseLight};
`;

interface Props {
  searchTerm: string | null;
  setSearchTerm: React.Dispatch<React.SetStateAction<string | null>>;
  totalCount: number;
}

const MasterDetailsSearchInput: React.FunctionComponent<Props> = ({
  searchTerm,
  setSearchTerm,
  totalCount,
}) => {
  return (
    <>
      <Flex flexBasis="290px">
        <SearchInput searchTerm={searchTerm} setSearchTerm={setSearchTerm} />
      </Flex>
      <VerticalLine></VerticalLine>
      <Box style={{ lineHeight: '20px' }}>
        <Text color="#7b8d93" fontSize={0}>{`${Number(
          totalCount
        ).toLocaleString()} ${pluralise(
          'result',
          'results',
          totalCount
        )}`}</Text>
      </Box>
    </>
  );
};

export default MasterDetailsSearchInput;
