import is from 'is_js';

export const required = (msg = 'This value is required') => {
  return (value: string) => {
    if (
      is.empty(value) ||
      is.not.existy(value) ||
      (typeof value === 'string' && is.empty(value.trim()))
    ) {
      return msg;
    }
    return null;
  };
};

export const number = (msg = 'This value must be a number') => {
  return (value: string) => {
    if (is.not.number(Number(value))) {
      return msg;
    }
    return null;
  };
};

export const integer = (msg = 'This value must be a integer') => {
  return (value: string) => {
    if (is.not.integer(Number(value))) {
      return msg;
    }
    return null;
  };
};

export const characterCount = (
  length: number,
  msg = 'The value must be a certain length'
) => {
  return (value: string) => {
    if (value.length !== length) {
      return msg;
    }
    return null;
  };
};

export const email = (msg = 'The value must be a valid email address') => {
  return (value: string) => {
    if (is.not.email(value)) {
      return msg;
    }
    return null;
  };
};

export const emailDomain = (msg = 'The value must be a valid email domain') => {
  return (value: string) => {
    if (is.not.email(`dummy@${value}`)) {
      return msg;
    }
    return null;
  };
};

export const ipAddress = (msg = 'The value must be a valid ipv4 address') => {
  return (value: string) => {
    if (is.not.ipv4(value)) {
      return msg;
    }
    return null;
  };
};

export const creditCard = (
  msg = 'The value must be a valid credit card number'
) => {
  return (value: string) => {
    if (is.not.creditCard(value)) {
      return msg;
    }
    return null;
  };
};

export const requiredObject = (msg = 'This value is required') => {
  return (value: any) => {
    for (let val of Object.values(value)) {
      if (is.empty(val) || is.not.existy(val)) {
        return msg;
      }
    }

    return null;
  };
};

export const maxLength = (
  length: number,
  msg = `The value exceeds the maximum length of ${length} characters`
) => {
  return (value: string) => {
    if (value.length >= length) {
      return msg;
    }
    return null;
  };
};

export const expectedCharacters = (
  expression: RegExp,
  msg = 'The value contains invalid characters'
) => {
  return (value: string) => {
    if (!expression.test(value)) {
      return msg;
    }
    return null;
  };
};

export const regexMatch = (
  expression: RegExp,
  msg = 'The value contains invalid characters'
) => {
  return (value: string) => {
    if (expression.test(value)) {
      return msg;
    }
    return null;
  };
};

export const duplicate = <Values>(
  otherkey: keyof Values,
  msg = 'The value must be a unique'
) => {
  return (value: Values[keyof Values], values: Values) => {
    if (value === values[otherkey]) {
      return msg;
    }
    return null;
  };
};
