import endpoints from 'api/endpoints';
import { useAddUpdateMutation } from '../common/useAddUpdateMutation';
import { ScoringRuleFormData } from 'types/models';
import { AddRuleResponse } from '.';

export const useUpdateRule = (onSuccess: () => void) => {
  return useAddUpdateMutation<ScoringRuleFormData, AddRuleResponse>(
    'put',
    endpoints.rules,
    {
      onSuccess,
    }
  );
};
