import useHeaders from '../useRequestHeaders';
import { post } from 'api/utils';
import useAuth from 'hooks/useAuth';
import { AxiosRequestConfig } from 'axios';
import { useCallback } from 'react';

const usePostRequest = <RequestType, ResponseType = {}>(
  url: string,
  defaultOptions: AxiosRequestConfig = {}
) => {
  const createHeaders = useHeaders();
  const auth = useAuth();

  const request = useCallback(
    async (
      data: RequestType,
      path?: string,
      options: AxiosRequestConfig = defaultOptions
    ) => {
      const headers = await createHeaders();

      const additionalPath = path ? `/${path}` : '';

      try {
        const response = await post<RequestType, ResponseType>(
          `${url}${additionalPath}`,
          data,
          {
            ...options,
            headers: {
              ...headers,
              ...(options && options.headers),
            },
          }
        );

        return response.data;
      } catch (error) {
        if (error.response.status === 401) {
          auth.logout();
        }
        throw error;
      }
    },
    [auth, createHeaders, defaultOptions, url]
  );

  return request;
};

export default usePostRequest;
