import useGetRequest from 'hooks/api/common/useGetRequest';
import { useQuery } from 'react-query';
import endpoints from 'api/endpoints';
import { AvailableProperty } from 'types/models';
import { AxiosError } from 'axios';

export interface AvailablePropertiesResponse {
  properties: AvailableProperty[];
}

export const useAvailablePropertiesData = () => {
  const req = useGetRequest<AvailablePropertiesResponse>();
  return useQuery<
    AvailablePropertiesResponse,
    AxiosError<AvailablePropertiesResponse>
  >([endpoints.rules, 'available-properties'], () =>
    req(`${endpoints.rules}/available-properties`)
  );
};
