import React from 'react';
import { TextField, ClickableElement } from '@cko/primitives';
import { IconActionSearch } from '@cko/icons';
import styled from 'styled-components';
import { space, SpaceProps } from 'styled-system';

const SearchIcon = styled(IconActionSearch)`
  align-self: center;
  margin: 0 8px 0 4px !important;
`;

const ClearButton = styled(ClickableElement)<SpaceProps>`
  font-size: 13px;
  color: #1da177;
  ${space}
`;

const ClearPlaceHolder = styled.div`
  width: 35px;
`;

interface Props {
  searchTerm: string | null;
  setSearchTerm: React.Dispatch<React.SetStateAction<string | null>>;
}

const SearchInput: React.FunctionComponent<Props> = ({
  searchTerm,
  setSearchTerm,
}) => {
  const prefix =
    searchTerm && searchTerm.length > 0 ? (
      <ClearButton
        data-cy="searchClearButton"
        px={1}
        onClick={() => {
          setSearchTerm('');
        }}
      >
        clear
      </ClearButton>
    ) : (
      <ClearPlaceHolder />
    );

  return (
    <TextField
      style={{ fontSize: '13px' }}
      prefix={(<SearchIcon />) as any}
      suffix={prefix}
      onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
        setSearchTerm(e.target.value);
      }}
      value={searchTerm || ''}
      data-cy="searchInput"
      placeholder="Search"
    />
  );
};

export default SearchInput;
