import { useQuery } from 'react-query';
import { ScoringRuleResponse } from 'types/models';
import endpoints from 'api/endpoints';
import useGetRequest from 'hooks/api/common/useGetRequest';
import { AxiosError } from 'axios';

export const useRulesData = () => {
  const req = useGetRequest<ScoringRuleResponse>();

  return useQuery<ScoringRuleResponse, AxiosError<ScoringRuleResponse>>(
    endpoints.rules,
    () => req(endpoints.rules)
  );
};
