import usePutRequest from 'hooks/api/common/usePutRequest';
import { useMutation, MutateConfig } from 'react-query';
import usePostRequest from './usePostRequest';
import { AxiosError } from 'axios';

const requestMap = {
  put: usePutRequest,
  post: usePostRequest,
};

export const useAddUpdateMutation = <RequestType, ResponseType = {}>(
  requestType: 'post' | 'put',
  endpoint: string,
  mutationOptions: MutateConfig<
    ResponseType,
    AxiosError<ResponseType>,
    { data: RequestType; path?: string }
  > = {}
) => {
  const useRequest = requestMap[requestType];
  const request = useRequest<RequestType, ResponseType>(endpoint);

  return useMutation<
    ResponseType,
    AxiosError<ResponseType>,
    { data: RequestType; path?: string }
  >(({ data, path }) => request(data, path), {
    ...mutationOptions,
    throwOnError: true,
  });
};
