import React from 'react';
import {
  Modal,
  Box,
  Text,
  TextInline,
  Flex,
  DangerButton,
  OutlineButton,
} from '@cko/primitives';
import InlineAlert from 'components/InlineAlert';

interface Props {
  itemName: string;
  itemType: string;
  handleClose: () => void;
  loading: boolean;
  onDelete: () => void;
  onCancel: () => void;
  submissionErrorMessage: string | null;
}

const DeleteConfirmationModal: React.FunctionComponent<Props> = ({
  handleClose,
  itemType,
  itemName,
  onDelete,
  onCancel,
  loading,
  submissionErrorMessage,
}) => {
  return (
    <Modal type="alert" title={`Delete ${itemType}`} closeModal={handleClose}>
      <Box pb={4}>
        <Text fontSize={0} pb={3}>
          Are you sure you want to delete{' '}
          <TextInline as="strong" fontWeight={500}>
            {itemName}
          </TextInline>
          ?
        </Text>
        <Text fontSize={0} fontWeight={400} color="#BE4343">
          This action is permanent.
        </Text>
      </Box>
      <Flex alignItems="center" p={0}>
        <Box>
          <DangerButton onClick={onDelete} data-testid="delete-button">
            {loading ? 'Deleting' : `Delete`}
          </DangerButton>
        </Box>
        <Box pl={3}>
          <OutlineButton onClick={onCancel}>Cancel</OutlineButton>
        </Box>
      </Flex>

      {submissionErrorMessage && (
        <InlineAlert>{submissionErrorMessage}</InlineAlert>
      )}
    </Modal>
  );
};

export default DeleteConfirmationModal;
