import endpoints from 'api/endpoints';
import { useAddUpdateMutation } from '../common/useAddUpdateMutation';
import { ScoringRuleFormData } from 'types/models';

export interface AddRuleResponse {
  id: string;
}

export const useAddRule = (
  onSuccess: () => void,
  onSettled: (values: ScoringRuleFormData & AddRuleResponse) => void
) => {
  return useAddUpdateMutation<ScoringRuleFormData, AddRuleResponse>(
    'post',
    endpoints.rules,
    {
      onSuccess,
      onSettled: (data, _, vars) => {
        data && onSettled({ ...data, ...vars.data });
      },
    }
  );
};
