import React, { memo } from 'react';
import Placeholder from './Placeholder';

interface Props {
  length: number;
}

const SidebarLinksPlaceholder: React.FC<Props> = ({ length }) => (
  <div data-cy="loading-placeholder">
    <Placeholder height={215} width={180}>
      {Array.from({ length }, (_, i) => {
        return (
          <rect
            key={i}
            x="0"
            y={i !== 0 ? (i * 215) / 6 : 0}
            rx="0"
            ry="0"
            width="100%"
            height="20"
          />
        );
      })}
    </Placeholder>
  </div>
);

export default memo(SidebarLinksPlaceholder);
