import { useMutation, MutateConfig } from 'react-query';
import useDeleteRequest from './useDeleteRequest';
import { AxiosError } from 'axios';

export const useDeleteMutation = <TResponse>(
  endpoint: string,
  mutationOptions: MutateConfig<TResponse, AxiosError, {}> = {}
) => {
  const request = useDeleteRequest(endpoint);

  return useMutation<TResponse, AxiosError<TResponse>, string>(
    (path) => request(path),
    {
      ...mutationOptions,
      throwOnError: true,
    }
  );
};
