import React from 'react';
import ContentLoader from 'react-content-loader';

interface Props {
  height: number;
  width: number;
  speed?: number;
}

const Placeholder: React.FC<Props> = ({
  height,
  width,
  speed = 2,
  children,
}) => (
  <ContentLoader
    height={height}
    width={width}
    speed={speed}
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
  >
    {children}
  </ContentLoader>
);

export default Placeholder;
