type Error = string | null;
type Errors<Values> = { [k in keyof Values]?: Error };

type Validator<Values> = (
  value: string,
  values: Values
) => string | null | Promise<string | null>;
type ValidatorOptions<V> = Record<keyof V, Validator<V>[]>;
type ValidatorFunc<Values> = (
  values: Values
) => Record<string, Error> | Promise<Record<string, Error>>;
export type ValidatorObj<K extends string, V> = Record<K, ValidatorFunc<V>>;

const validateValue = async <Values>(
  value: string,
  validators: Validator<Values>[],
  values: Values
) => {
  for (const validator of validators) {
    const result = await validator(value, values);
    if (result !== null) {
      return result;
    }
  }

  return null;
};

const validateValues = async <Values extends Record<string, any>>(
  values: Values,
  validatorOptions: ValidatorOptions<Values>
) => {
  const errors: Errors<Values> = {};
  const keys = Object.keys(validatorOptions) as (keyof Values)[];

  for (let index = 0; index < keys.length; index++) {
    const key = keys[index];
    const validators = validatorOptions[key];
    const errorMessage = await validateValue<Values>(
      values[key],
      validators,
      values
    );

    if (errorMessage !== null) {
      errors[key] = errorMessage;
    }
  }

  return errors;
};

const createValidator = <Values>(
  validatorOptions: ValidatorOptions<Values>
) => {
  return async (values: Values) => {
    return await validateValues<Values>(values, validatorOptions);
  };
};

export default createValidator;
