import React from 'react';
import { IconActionDelete } from '@cko/icons';
import styled from 'styled-components';
import { variant } from 'styled-system';

import ClickableElement from 'components/ClickableElement';

interface Props {
  onClick: (event: React.MouseEvent<HTMLElement>) => void;
  dataCy?: string;
  variant?: 'large' | 'medium';
}

const sizing = {
  large: '32px',
  medium: '26px',
};

const Button = styled(ClickableElement)<Props>`
  ${variant({
    variants: {
      medium: { height: `${sizing.medium}`, width: `${sizing.medium}` },
      large: { height: `${sizing.large}`, width: `${sizing.large}` },
    },
  })}
  border: 1px solid ${(props) => props.theme.colors.baseLight};
  border-radius: 3px;
  background-color: #ffffff;
  font-size: 16px;
  line-height: 1em;

  table & {
    opacity: 0;
  }

  tr:hover &,
  &:focus {
    opacity: 1;
  }

  &:hover {
    box-shadow: 0 1px 3px 0 rgba(123, 141, 147, 0.15);
  }
`;

const DeleteButton: React.FunctionComponent<Props> = ({
  onClick,
  dataCy = 'delete-button',
  variant = 'medium',
}) => (
  <Button data-cy={dataCy} onClick={onClick} variant={variant}>
    <IconActionDelete />
  </Button>
);

export default DeleteButton;
